.calculator {
    width: 300px;
    margin: 50px auto;
    border: 2px solid #000;
    border-radius: 10px;
    padding: 10px;
    background: #f4f4f4;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .display {
    background: #333;
    color: #fff;
    font-size: 2rem;
    text-align: right;
    padding: 10px;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  
  .buttons {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
  }
  
  button {
    font-size: 1.2rem;
    padding: 15px;
    background: #eee;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background 0.3s;
  }
  
  button:hover {
    background: #ddd;
  }
  
  button.equals {
    grid-column: span 2;
    background: #ff9500;
    color: #fff;
  }
  
  button.equals:hover {
    background: #e08900;
  }
  
  button.clear {
    background: #ff3b30;
    color: #fff;
  }
  
  button.clear:hover {
    background: #d73227;
  }
  .heading {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 20px;
    color: #333;
    text-transform: uppercase;
  }
    